import { Account } from "../../../hooks/use_account";
import { SmartState } from "../../../hooks/use_smart_state";
import { securePage } from "../../../components/common/secure_page";
import Form from "../../../components/common/form/form";
import Title from "../../../components/common/form/title";
import { formParentManageAccount } from "../manage-account";
import Box from "../../../components/common/form/box";
import P from "../../../components/common/form/p";
import Input from "../../../components/common/form/input";
import SubmitButton from "../../../components/common/form/submit-button";
import { formRequestAccountManagerAccountName } from "../../../lib/shared/form-request-makeup/requests/account/manage-account/name";

export type AccountManageAccountNamePageProps = {
    result: {};
    account: SmartState<Account | null>;
};

const AccountManageAccountNamePage = securePage(function({result,account}: AccountManageAccountNamePageProps) {
    const { user: { firstName,lastName } } = account.value;

    return (<>
        <div className="account-manage-account-name-page">            
            <Form 
                requestMakeup={formRequestAccountManagerAccountName}
                parent={formParentManageAccount}
                autoFocus
                input={{
                    maxWidth: 400
                }}
                onSuccess={() => {
                    return {
                        type: "redirect",
                        path: "/account/manage-account",
                        message: {
                            type: "info",
                            message: "The name associated with your account has been updated."
                        }
                    };
                }}
            >
                <Title>Change your name</Title>
                <Box border>
                    <P>
                        If you want to change the name associated with your TBD Liquids account, you can do so below.
                    </P>
                    <Input name="firstName" initialValue={firstName} />
                    <Input name="lastName" initialValue={lastName} />
                    <SubmitButton caption="Save" disableIfUnchanged={["firstName","lastName"]} />
                </Box>
            </Form>
        </div>
        <style jsx>{`
            .account-manage-account-name-page {
                /* min-height: 600rem; */
                width: 100%;
            }
        `}</style>
    </>
);
});

export default AccountManageAccountNamePage;
